<template>
  <div class="kukudoge">
    <p class="log">{{ log }}</p>
    <div class="game-space" :style="resolveGameSpaceWidthHeight()">
      <!-- Ë£§Ë£§ÁöÑ‰ΩçÁΩÆ -->
      <div class="kuku" :style="resolvePositonAndDirection()" ref="kuku">üêï</div>
      <!-- Âú∞Âõæ -->
      <div class="map">
        <div v-for="(col, i) in gameSpace[0]" :key="i" class="game-space-h">
          <div v-for="(row, j) in gameSpace[1]" :key="j" class="game-space-w" :class="resolveCellKind(i, j)"></div>
        </div>
      </div>
    </div>
    <!-- Áä∂ÊÄÅÊèêÁ§∫ -->
    <div class="kuku-state">
      <p class="tips">Ë£§Â≠ò:<span v-for="(item, i) in kuku.shits" :key="i">üí©</span></p>
    </div>
    <!-- PC UI -->
    <div class="tips-content">
      <p class="tips">‚¨ÖÔ∏è‚û°Ô∏èÁßªÂä®Ôºåüîº‰∏äÊ•ºÔºå ‚¨áÔ∏è‰∏ãÊ•º/Êãâüí©</p>
    </div>
    <!-- ÁßªÂä®Á´Ø UI -->
    <div class="mobile-ui">
      <div class="line">
        <div class="button" @click="kukuGo('left')">‚¨ÖÔ∏è</div>
        <div class="button" @click="kukuShit()">üí©</div>
        <div class="button" @click="kukuGo('right')">‚û°Ô∏è</div>
      </div>
      <div class="line">
        <div class="button" @click="kukuGoDown()">‚¨áÔ∏è</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      gameSpace: [10, 6], // Ê∏∏ÊàèÁΩëÊ†ºÁöÑÊï∞Èáè [ Ê®™, Á∫µÂêë]
      gameCell: [30, 1], // Ê∏∏ÊàèÂçï‰∏™ÁΩëÊ†ºÁöÑÂ§ßÂ∞è [Â§ßÂ∞è,ÁΩëÊ†ºÈó¥ÈöîÂ∞∫ÂØ∏]
      kuku: {
        // Ë£§Ë£§ÁöÑÂ±ûÊÄßÂèÇÊï∞
        position: [0, 0], // Ë£§Ë£§ÁöÑ‰ΩçÁΩÆÂèÇÊï∞
        direction: 1, // Ë£§Ë£§ÁöÑÊúùÂêëÔºå1 ‰ª£Ë°®Âè≥Ôºå-1 ‰ª£Ë°®Â∑¶
        shits: 4, // ËøòÂèØ‰ª•ÊãâÁöÑÊ¨°Êï∞
      },
      cellKind: [
        // ËÆ∞ÂΩïÂú∞ÂΩ¢ 0 ‰ª£Ë°®Á©∫Ôºå1 ‰ª£Ë°®Âú∞Èù¢Ôºå 2 ‰ª£Ë°®Â∫äÔºå3 ‰ª£Ë°®Á≤ëÁ≤ë  Ôºå4 ‰ª£Ë°®Ê¢ØÂ≠ê
        '0000000000/',
        '1111101141/',
        '0000000040/',
        '1114110111/',
        '0004000002/',
        '1111110111/',
      ],
      log: 'ÊéßÂà∂‰∏ÄÂè™Âè´„ÄêË£§Ë£§„ÄëÁöÑ üêï Êää üí©ÊãâÂú® üõå‰∏ä', // logo
    }
  },
  created: function () {
    // Ê∑ªÂä†ÂÖ®Â±ÄÂø´Êç∑ÈîÆÁõëÂê¨
    var _this = this
    document.onkeydown = function () {
      let key = window.event.keyCode
      if (key == 32 || key == 37 || key == 38 || key == 39 || key == 40) {
        _this.onType(key)
      }
    }
  },
  methods: {
    resolvePositonAndDirection() {
      //ËÆ°ÁÆóË£§Ë£§ÁöÑÂùêÊ†áÂíåÊúùÂêë
      const x = this.kuku.position[0] * (this.gameCell[0] + 2 * this.gameCell[1])
      const y = this.kuku.position[1] * (this.gameCell[0] + 2 * this.gameCell[1])
      const direction = this.kuku.direction
      return 'left:' + x + 'px; top:' + y + 'px;' + 'transform:scaleX(' + -1 * direction + ')'
    },
    resolveGameSpaceWidthHeight() {
      //ËÆ°ÁÆóÂú∞ÂõæÁöÑÂ§ßÂ∞è
      const width = (this.gameCell[0] + 2 * this.gameCell[1]) * this.gameSpace[0]
      const height = (this.gameCell[0] + 2 * this.gameCell[1]) * this.gameSpace[1]
      return 'width:' + width + 'px; height:' + height + 'px;'
    },
    resolveCellKind(i, j) {
      //ÁªòÂà∂Âú∞ÂΩ¢
      let kind = 0
      if (j < this.cellKind.length) {
        kind = this.cellKind[j][i]
      } else {
        kind = 1
      }
      return 'cell-kind-' + kind
    },
    onType(key) {
      //ÁõëÂê¨Âø´Êç∑ÈîÆ 38 ‰ª£Ë°®‰∏äÔºå40 ‰ª£Ë°®‰∏ãÔºå37 ‰ª£Ë°®Â∑¶Ôºå39 ‰ª£Ë°®Âè≥Ôºå32 ‰ª£Ë°®Á©∫Ê†º
      switch (key) {
        case 37:
          this.kukuGo('left')
          break
        case 38:
          this.kukuClimbUp()
          break
        case 40:
          this.kukuDown()
          break
        case 39:
          this.kukuGo('right')
          break
        case 32:
          this.log = 'Ë£§Ë£§ÊÉ≥Ë∑≥Ôºå‰ΩÜÊòØÊàëÊ≤°ÂÅöËøô‰∏™ÂäüËÉΩ'
          break
        default:
      }
    },
    kukuGo(direction) {
      // Ë£§Ë£§ÁöÑË°åÂä®
      let target = [] //ÊÉ≥ÂéªÂì™
      let directionIsOk = false //ÊñπÂêëÊòØÂê¶Ê≠£Á°Æ
      if (direction == 'left') {
        //ÂéªÂ∑¶ËæπÁöÑËØù
        target = [this.kuku.position[0] - 1, this.kuku.position[1]]
        if (this.kuku.direction == -1) {
          directionIsOk = true
        } else {
          this.kuku.direction = -1 //ÂêëÂ∑¶ËΩ¨
        }
      } else if (direction == 'right') {
        //ÂéªÂè≥ËæπÁöÑËØù
        target = [this.kuku.position[0] + 1, this.kuku.position[1]]
        if (this.kuku.direction == 1) {
          directionIsOk = true
        } else {
          this.kuku.direction = 1 //ÂêëÂè≥ËΩ¨
        }
      }
      if (this.canKukuGo(target) && directionIsOk) {
        //ÊµãËØïËÉΩÂéª
        this.$set(this.kuku, 'position', [target[0], target[1]])
        this.log = 'Ë£§Ë£§ÁßªÂä®‰∫Ü'
      } else {
        //Â¶ÇÊûúÊ≤°ÊúâÁßªÂä®
        if (directionIsOk) {
          //ÊòØ‰∏çËÉΩÂéªÁöÑÂú∞Êñπ
          this.log = 'Ë£§Ë£§ÊÉ≥Âéª‰∏çËÉΩÂéª'
        } else {
          //Êç¢‰∏™ÊñπÂêë
          this.log = 'Ë£§Ë£§ÊéâÂ§¥‰∫Ü'
        }
      }
    },
    canKukuGo(target) {
      // Ë£§Ë£§ËÉΩË°å‰πà
      if ((this.isTargetBlank(target) || this.isTargetLadder(target)) && this.isTargetNotCliff(target)) {
        return true
      } else {
        return false
      }
    },
    isTargetLadder(target) {
      //ÂâçÈù¢ÊòØÊ¢ØÂ≠ê
      const cellTarget = this.cellKind[target[1]][target[0]]
      if (this.isInMap(target) && cellTarget === '4') {
        return true
      } else {
        return false
      }
    },
    isTargetBlank(target) {
      //ÂâçÈù¢ÊòØÁ©∫Âú∞
      const cellTarget = this.cellKind[target[1]][target[0]]
      if (this.isInMap(target) && cellTarget === '0') {
        return true
      } else if (cellTarget === '2') {
        return true
      } else {
        return false
      }
    },
    isInMap(target) {
      //Âú®Âú∞Âõæ‰∏≠
      if (target[0] >= 0 && target[1] >= 0 && target[0] < this.gameSpace[0] && target[1] < this.gameSpace[1]) {
        return true
      } else {
        return false
      }
    },
    isTargetNotCliff(target) {
      //ÂâçÈù¢‰∏çÊòØÊÇ¨Â¥ñ
      const cellUnderTarget = this.cellKind[target[1] + 1][target[0]]
      if (cellUnderTarget !== '0') {
        return true
      } else {
        return false
      }
    },
    targetIsBed(target) {
      //ÂâçÈù¢ÊòØÂ∫ä
      const cellTarget = this.cellKind[target[1]][target[0]]
      if (cellTarget === '2') {
        return true
      } else {
        return false
      }
    },
    kukuShit() {
      //ÊéßÂà∂Ë£§Ë£§ÊãâÁ≤ëÁ≤ë
      let target = [this.kuku.position[0] - this.kuku.direction, this.kuku.position[1]]
      if (this.targetAllows(target)) {
        this.kuku.shits -= 1
        target = this.testTarget(target)
        if (this.targetIsBed(target)) {
          this.startShit(target)
          this.log = 'üí©ÊãâÂ∫ä‰∏ä‰∫Ü!!!!!!!!!!!!!!'
          alert('YOU WIN : üí©ÊãâÂ∫ä‰∏ä‰∫Ü!!!!!!!!!!!!!!')
        } else {
          this.startShit(target)
        }
      } else {
        this.log = '‰∏çËÉΩÂú®ËøôÈáåÊãâ'
      }
    },
    startShit(target) {
      //ÂºÄÂßãÊãâ
      this.log = 'Ë£§Ë£§ÊãâÂ±é‰∫Ü' //ËØ¥Êòé
      this.changeCell(target, 3) //ÂèòÂåñÂú∞ÂΩ¢
    },
    targetAllows(target) {
      //ÂΩìÂâçÂú∞ÂΩ¢ÊòØÂê¶ÂÖÅËÆ∏Êãâ
      if (this.isTargetBlank(target) || this.isTargetLadder(target)) {
        return true
      } else {
        return false
      }
    },
    changeCell(target, _kind) {
      //Âú∞ÂΩ¢ÂèòÂåñÂáΩÊï∞
      const arr = this.cellKind[target[1]] //Ë¶ÅÂèòÂåñÁöÑÊï∞ÊçÆ
      this.cellKind[target[1]] = this.replaceArr(arr, target[0] + 1, 3)
    },
    replaceArr(arr, order, to) {
      return arr.substring(0, order - 1) + to + arr.substring(order)
    },
    testTarget(target) {
      if (!this.isTargetNotCliff(target)) {
        return [target[0], target[1] + 1]
      } else {
        return target
      }
    },
    kukuClimbUp() {
      if (this.canUp()) {
        this.log = 'Âêë‰∏äÁà¨'
        const target = [this.kuku.position[0], this.kuku.position[1] - 1]
        this.$set(this.kuku, 'position', [target[0], target[1]])
      }
    },
    canUp() {
      const now = [this.kuku.position[0], this.kuku.position[1]]
      if (this.kuku.position[1] > 0 && this.isTargetLadder(now)) {
        return true
      } else {
        this.log = 'Ë£§Ë£§ÊÉ≥‰∏äÂéªÔºå‰ΩÜÊòØ‰∏ä‰∏çÂéª'
        return false
      }
    },
    kukuDown() {
      const target = [this.kuku.position[0], this.kuku.position[1] + 1]
      if (this.isTargetLadder(target)) {
        this.log = 'Ë£§Ë£§‰∏ãÊ•º‰∫Ü'
        this.$set(this.kuku, 'position', [target[0], target[1]])
      } else {
        if (this.kuku.shits > 0) {
          this.kukuShit()
        } else {
          this.log = 'Ê≤°ÊúâÊñô‰∫Ü'
        }
      }
    },
    kukuGoDown() {
      const target = [this.kuku.position[0], this.kuku.position[1] + 1]
      if (this.isTargetLadder(target)) {
        this.log = '‰∏ãÊ•º‰∫Ü'
        this.$set(this.kuku, 'position', [target[0], target[1]])
      } else {
        this.log = 'Ë£§Ë£§ÊÉ≥‰∏ãÂéªÔºå‰∏ã‰∏çÂéª'
      }
    },
  },
}
</script>

